<template>
  <li class="add_project_wrap mr-4">
    <b-button class="px-4 py-3" id="addProBtn" @click="onCancel" variant="light">
      <i class="fa fa-plus-square-o pr-1" aria-hidden="true"></i>
      {{ $t('addProject.addProject') }}
      &nbsp;<i class="fa fa-caret-down pl-1" aria-hidden="true"></i>
    </b-button>
    <b-collapse id="add-project" class="mt-2">
      <b-card class="add_project_card">
        <template v-slot:header>
          <div class="d-flex justify-content-between">
            <h6 class="mb-0">{{ $t('addProject.addProject') }}</h6>
            <b-button-close @click="onCancel"></b-button-close>
          </div>
        </template>
        <b-form>
          <!-- Name (required) ================== -->
          <b-form-group @submit.stop.prevent>
            <label for="dropdown-form-name">Name <span class="red">*</span></label>
            <b-form-input
              v-model="form.name"
              :state="validation.name.state"
              aria-describedby="name-feedback"
              id="dropdown-form-name"
              size="sm"
              :placeholder="$t('addProject.proNamePlaceholder')"
            />
            <b-form-invalid-feedback id="name-feedback">
              {{ validation.name.text }}
            </b-form-invalid-feedback>
          </b-form-group>

          <!-- Display Name ================== -->
          <b-form-group label="Display Name" label-for="dropdown-form-dispaly-name">
            <b-form-input
              id="dropdown-form-dispaly-name"
              v-model="form.displayed"
              size="sm"
              :placeholder="$t('addProject.proDisplayNamePlaceholder')"
            />
          </b-form-group>

          <!-- Description ================== -->
          <b-form-group label="Description" label-for="dropdown-form-description">
            <b-form-input
              id="dropdown-form-description"
              v-model="form.description"
              size="sm"
              :placeholder="$t('addProject.proDescPlaceholder')"
            />
          </b-form-group>

          <b-dropdown-divider />

          <!-- Storage ================== -->
          <b-form-group label="Storage" label-for="dropdown-form-storage">
            <b-form-input
              id="dropdown-form-storage"
              v-model="_storageName"
              size="sm"
              placeholder="my-project-storage"
              disabled
            />
          </b-form-group>

          <!-- Storage Size ================== -->
          <b-form-group label="Storage Size" label-for="dropdown-form-storage-size">
            <b-form-input
              id="dropdown-form-storage-size"
              v-model="form.storageSize"
              size="sm"
            />
          </b-form-group>
        </b-form>

        <template v-slot:footer>
          <div class="d-flex justify-content-end">
            <b-button size="sm" class="mr-2" @click="onCancel">{{ $t('common.cancel') }}</b-button>
            <b-button size="sm" variant="primary" @click="onCreate">{{ $t('common.create') }}</b-button>
          </div>
        </template>
      </b-card>
    </b-collapse>

    <!-- error message -->
    <one-button-alert name="errorModal" ref="errorModal" />
  </li>
</template>

<script>
import conf from '../../config'
import { mapActions } from 'vuex'
import OneButtonAlert from '../../components/modal/OneButtonAlert.vue'
import Validation from '../../components/mixin/Validation'

export default {
  name: 'addProject',
  components: {
    OneButtonAlert
  },
  mixins: [ Validation ],
  data () {
    return {
      form: {
        name: '',
        displayed: '',
        description: '',
        storageName: '',
        storageSize: conf.defaultStorageSize
      },
      validation: {
        name: { state: undefined, text: '' }
      },
      isJustShown: false
    }
  },
  mounted () {
    // TODO 페이지 이동시 collapse가 열려 있으면 닫히도록 수정해야함
    // this.$root.$once('bv::collapse::state', (collapseId, isJustShown) => {
    //   if (isJustShown) {
    //     this.$root.$emit('bv::toggle::collapse', 'add-project')
    //   }
    // })
  },
  computed: {
    _storageName () {
      return this.form.name + '-storage'
    }
  },
  methods: {
    ...mapActions('project', ['addProject']),
    ...mapActions('storage', ['addStorage']),
    onCancel () {
      this.$root.$emit('bv::toggle::collapse', 'add-project')
      this.form.name = ''
      this.form.displayed = ''
      this.form.description = ''
      this.form.storageName = this._storageName
      this.form.storageSize = conf.defaultStorageSize
      this.validation.name.state = undefined
      this.validation.name.text = ''
    },
    onCreate () {
      if (this.addProjectValidation()) {
        this.addProject(this.form)
          .then(response => {
            this.addStorage({
              projectName: this.form.name,
              storageName: this.form.storageName,
              storageSize: this.form.storageSize
            })
            this.$root.$emit('bv::toggle::collapse', 'add-project')
          })
          .catch(error => {
            this.$refs.errorModal.show({
              title: 'Error Message',
              text: error
            })
          })
      }
    }
  }
}
</script>

<style scoped>
.add_project_wrap{
  position: relative;
}
#add-project{
  position: absolute;
  top: 50px;
  right: 0px;
  box-shadow: 0px 0px 7px rgba(0,0,0,0.3);
}
.add_project_card{
  width: 400px;
}
span.red{
  color: #ff0000;
}
#addProBtn{
  border-radius: 0px;
  background-color: transparent;
  border: none;
  outline: none;
  box-shadow: none;
}
#addProBtn:hover{
  background-color: #f0f3f5;
}
</style>
