export default {
  data: function () {
    return {
      emailReg: /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i,
      trimReg: /\s/g,
      enReg: /^[a-z0-9+-]*$/
    }
  },
  methods: {
    addProjectValidation: function () {
      this.validation.name.state = undefined
      // projectName Null Check
      if (this.form.name === '') {
        this.validation.name.text = this.$i18n.t('addProject.proNameNullValidate')
        this.validation.name.state = false
        return false
      }
      // projectName trim check
      if (this.trimReg.test(this.form.name)) {
        this.$log.info(this.trimReg.test(this.form.name))
        this.validation.name.text = this.$i18n.t('addProject.proNameTrimValidate')
        this.validation.name.state = false
        return false
      }
      // projectName english check
      if (!this.enReg.test(this.form.name)) {
        this.validation.name.text = this.$i18n.t('addProject.proNameEnValidation')
        this.validation.name.state = false
        return false
      }
      return true
    }
  }
}
