<template>
  <b-dropdown id="project-dropdown-list"
    v-model="selected"
    toggle-class="px-4 py-3 proListBtn"
  >
    <template v-slot:button-content>
      <span class="pr-2 proListText">{{ selected }}</span>
    </template>
    <b-dropdown-item v-for="item in projects.items"
      :key="item.metadata.name"
      :value="item.metadata.name"
      :active="isActive(item.metadata.name)"
      @click="selected = item.metadata.name">
      {{item.metadata.name}}
    </b-dropdown-item>
  </b-dropdown>
</template>

<script>
import { mapActions, mapState, mapGetters } from 'vuex'

export default {
  name: '',
  props: {
  },
  data () {
    return {
      projects: {
        items: []
      },
      selected: ''
    }
  },
  mounted () {
    this.requestLists()
  },
  computed: {
    ...mapState('project', ['currentProject', 'projectList']),
    ...mapGetters('project', ['getProjectCount']),
    projectCountUpdated () {
      return this.getProjectCount
    }
  },
  methods: {
    ...mapActions('project', ['getProjectList', 'setCurrentProject']),
    requestLists () {
      this.getProjectList().then((list) => {
        this.projects = list
        this.selected = this.currentProject
      })
    },
    isActive (name) {
      return (this.currentProject === name)
    }
  },
  watch: {
    selected () {
      if (this.currentProject !== this.selected) {
        this.setCurrentProject(this.selected)
        this.$router.push({
          name: 'main',
          params: { projectName: this.selected }
        })
      }
    },
    projectCountUpdated () {
      this.projects = this.projectList
    },
    currentProject () {
      if (this.currentProject !== this.selected) {
        this.selected = this.currentProject
      }
    }
  }
}
</script>

<style>
.proListBtn{
  border-radius: 0px;
  background-color: transparent;
  border: none;
  outline: none;
  box-shadow: none;
  box-sizing: border-box;
}
.show .proListBtn{
  border-radius: 0px;
  background-color: transparent;
  border: none;
  outline: none;
  box-shadow: none;
  box-sizing: border-box;
}
.proListBtn:hover{
  background-color: #f0f3f5;
}
</style>
