<template>
  <div class="app">

    <!-- header -->
    <AppHeader fixed>
      <b-link class="navbar-brand" to="/">
        <img class="navbar-brand-full" src="img/lg-logo.png" width="89" height="25" alt="LG Logo">
        <img class="navbar-brand-minimized" src="img/lg-logo-sm.png" width="30" height="30" alt="LG Logo">
      </b-link>
      <!-- SidebarToggler -->
      <SidebarToggler class="d-lg-none" display="md" mobile />
      <SidebarToggler class="d-md-down-none" display="lg" />
      <!-- Project List -->
      <ProjectDropDownList />
      <!-- RightSideBar -->
      <b-navbar-nav class="ml-auto">
        <!--Add Project-->
        <AddProject />
        <!-- log-in -->
        <b-nav-item class="d-block">
          <i class="fa fa-user"></i>
          {{ userName }}
        </b-nav-item>
        <b-nav-item class="d-block" v-b-tooltip.hover title="로그아웃" @click="logoutConfirm">
          <i class="fa fa-sign-out"></i>
        </b-nav-item>
      </b-navbar-nav>
    </AppHeader>

    <!-- main -->
    <div class="app-body">
      <!-- 오른쪽 메뉴 -->
      <AppSidebar fixed>
        <SidebarHeader />
        <SidebarForm />
        <SidebarNav :nav-items="menuItems" />
        <SidebarFooter />
        <SidebarMinimizer />
      </AppSidebar>
       <!-- main -->
      <main class="main">
        <Breadcrumb :list="list" />
        <router-view />
      </main>
    </div>

    <!--footer-->
    <TheFooter>
      <div>
        <span class="ml-1">Robot Platform</span>
      </div>
      <div class="ml-auto">
        <span class="mr-1">Copyright &copy; 2019 LG Electronics. All Rights Reserved.</span>
      </div>
    </TheFooter>
    <!-- one button modal -->
    <one-button-alert
      ref="oneBtnModal"
      @submit="logoutSubmit"
    />
  </div>
</template>

<script>
import menu from '@/menu'
import {
  Header as AppHeader,
  SidebarToggler,
  Sidebar as AppSidebar,
  SidebarFooter,
  SidebarForm,
  SidebarHeader,
  SidebarMinimizer,
  SidebarNav,
  Breadcrumb,
  Footer as TheFooter
} from '@coreui/vue'

import { mapState, mapActions } from 'vuex'
import OneButtonAlert from '../../components/modal/OneButtonAlert.vue'
import ProjectDropDownList from '../../components/dropdown/ProjectDropDownList.vue'
import AddProject from '../../components/modal/AddProject.vue'

export default {
  name: 'HomeView',
  components: {
    AppHeader,
    AppSidebar,
    TheFooter,
    SidebarForm,
    SidebarFooter,
    SidebarToggler,
    SidebarHeader,
    SidebarNav,
    Breadcrumb,
    SidebarMinimizer,
    OneButtonAlert,
    ProjectDropDownList,
    AddProject
  },
  props: {
  },
  data () {
    return {
      userID: 'site_admin',
      password: 'Secudium!@#',
      isWorking: false
    }
  },
  computed: {
    ...mapState('user', ['user']),
    ...mapState('project', ['currentProject', 'currentPackage']),
    userName () {
      if (this.user) {
        return this.user.usrNm
      } else {
        return 'unknown'
      }
    },
    menuItems () {
      return menu.items
    },
    list () {
      let _router = this.$route.matched.filter((route) => route.name || route.meta.label)
      _router.forEach(r => {
        if (r.name === 'main') {
          r.meta.label = this.currentProject
        } else if (r.name === 'detail') {
          r.meta.label = this.currentPackage
        }
      })
      return _router
      // return this.$route.matched.filter((route) => route.name || route.meta.label)
    }
  },
  methods: {
    ...mapActions('user', ['logout']),
    logoutConfirm () {
      this.$refs.oneBtnModal.show({
        title: '로그아웃',
        text: '정말 로그아웃 하시겠습니까?',
        confirm: true
      })
    },
    logoutSubmit: async function () {
      if (!this.isWorking) {
        this.isWorking = true
        this.logout({ withCredentials: true })
          .then((data) => {
            this.$log.log('logout', data)
            this.$router.replace({ path: '/login' })
          }).catch((err) => {
            this.$log.info('logout fail', err)
            if (err.response !== undefined) {
              this.$refs.oneBtnModal.show({
                title: '실패',
                text: err.response.data,
                confirm: false
              })
              this.isWorking = false
            } else {
              this.$refs.oneBtnModal.show({
                title: 'Error',
                text: '로그아웃 중 에러가 발생했습니다. 관리자에게 문의 주세요 [error code : ' + err + ']',
                confirm: false
              })
              this.isWorking = false
            }
          })
        this.$refs.oneBtnModal.hide()
      }
    }
  }
}
</script>
